import { AbstractControl, ValidatorFn } from "@angular/forms";


export interface GEOJSON {
  lx:number,
  ly:number,
  mx:number,
  my:number
}


//
export function validatorsByReg(type : string, regexp : RegExp) : ValidatorFn {
  return (control : AbstractControl) : { [key: string] : any } => {
    let controlVal
    try{
      controlVal = control.value.toString();
    }catch{
      return null
    }
    let resObj = {}
    resObj[type] = controlVal
    return regexp.test(controlVal)? null : resObj;
  }
}

export function idcardValidators(type: string) : ValidatorFn {
  return (control : AbstractControl) : { [key: string] : any } => {
    let controlVal
    try{
      controlVal = control.value.toString();
    }catch{
      return null
    }
    let resObj = {}
    resObj[type] = controlVal
    if(type === 'HKID'){
      return IsHKID(controlVal)? null : resObj;
    }else if(type === 'CHID'){
      return IsCHID(controlVal)? null : resObj;
    }else if(type === 'MOID'){
      return IsMOID(controlVal)? null : resObj;
    }else{
      return IsOthersID(controlVal)? null : resObj;
    }
    
  }
}

export function IsCHID(code){
  let city = {
      11 : "",
      12 : "",
      13 : "",
      14 : "",
      15 : "",
      21 : "",
      22 : "",
      23 : " ",
      31 : "",
      32 : "",
      33 : "",
      34 : "",
      35 : "",
      36 : "",
      37 : "",
      41 : "",
      42 : " ",
      43 : "",
      44 : "",
      45 : "",
      46 : "",
      50 : "",
      51 : "",
      52 : "",
      53 : "",
      54 : " ",
      61 : "",
      62 : "",
      63 : "",
      64 : "",
      65 : "",
      71 : "",
      81 : "",
      82 : "",
      91 : " "
  };
  let pass = true;
  
  //
  let chidPat = /^[1-9][0-9]{5}(19[0-9]{2}|20[0-9]{2})(0[1-9]|1[0-2])(0[1-9]|[12][0-9]|3[01])[0-9]{3}[0-9xX]$/i;
  let matchArray = code.match(chidPat)
 
  if (!code || !matchArray) {
      return pass = false;
  }else {
    matchArray = matchArray.splice(1,3)
    if(new Date().getTime() - new Date(matchArray.join('/')).getTime() < 0 ){
      return pass = false
    }
  }

  if (!city[code.substr(0, 2)]) {
      return pass = false;
  } else {
      // 18
      if (code.length == 18) {
          code = code.split('');
          // 
          let factor = [ 7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2];
          // 
          let parity = [ 1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2 ];
          let sum = 0;
          let ai = 0;
          let wi = 0;
          for (let i = 0; i < 17; i++) {
              ai = code[i];
              wi = factor[i];
              sum += ai * wi;
          }
          let last = parity[sum % 11];
          if (last != code[17]) {
              return pass = false;
          }
      }
  }
  return pass;
}

//
function IsHKID(str) {
  let strValidChars = "ABCDEFGHIJKLMNOPQRSTUVWXYZ"
  if (str.length < 8)
  return false;
  // 
  if (str.charAt(str.length-3) == '(' && str.charAt(str.length-1) == ')'){
    str = str.substring(0, str.length - 3) + str.charAt(str.length -2);
  } 
  // 
  str = str.toUpperCase();

  // 
  let hkidPat = /^([A-Z]{1,2})([0-9]{6})([A0-9])$/;
  let matchArray = str.match(hkidPat);
  // not match, return false
  if (matchArray == null)
  return false;

  // 3
  let charPart = matchArray[1]; 
  let numPart = matchArray[2];
  let checkDigit = matchArray[3];

  // 
  let checkSum = 0;
  if (charPart.length == 2) {
    checkSum += 9 * (10 + strValidChars.indexOf(charPart.charAt(0)));
    checkSum += 8 * (10 + strValidChars.indexOf(charPart.charAt(1)));
  } else {
    checkSum += 9 * 36;
    checkSum += 8 * (10 + strValidChars.indexOf(charPart));
  }
  // 
  for (let i = 0, j = 7; i < numPart.length; i++, j--){
    checkSum += j * numPart.charAt(i);
  }
  
  // verify the check digit
  let remaining = checkSum % 11;
  let verify = remaining == 0 ? 0 : 11 - remaining;
  return verify == checkDigit || (verify == 10 && checkDigit == 'A');
}

function IsMOID(code){
  let moidPat = /^[1|5|7][0-9]{6}\([0-9Aa]\)/
  return moidPat.test(code)
}

function IsOthersID(code){
  let pat = /^.{1,20}$/
  return pat.test(code)
}




export function svgPathToGeoJson(path : string){
  
  let pathData = path.split(/(?=[MC])/);
  let resultArray = [];
  let tempCacheObj : GEOJSON = {
    mx:-1,
    my:-1,
    lx:-1,
    ly:-1
  };
  for(let i=1; i<pathData.length; i++) {
    if(pathData[i].includes('M')){
      let temp = pathData[i].split(' ')[1].split(',');
      tempCacheObj.mx = parseFloat(temp[0]);
      tempCacheObj.my = parseFloat(temp[1]);
    }
    
    if(pathData[i].includes('C')){
      let temp = pathData[i].split(' ')[3].split(',');
      tempCacheObj.lx = parseFloat(temp[0]);
      tempCacheObj.ly = parseFloat(temp[1]);
    }

    if(checkIsObjFull(tempCacheObj)){
      resultArray.push(Object.assign({},tempCacheObj));
      tempCacheObj.mx = -1;
      tempCacheObj.my = -1;
      tempCacheObj.lx = -1;
      tempCacheObj.ly = -1;
    }
  }

  return resultArray;
}

function checkIsObjFull(obj){
  if(typeof obj !== 'object'){
    return false
  }

  for(let attr in obj){
    if((!obj[attr]&&obj[attr]!==0) || obj[attr]<0){
      return false
    }
  }

  return true
}